							<view class="box" v-for="(item, i1) in box.info" :key="i1">
								<image  @click="toCategory(item.son_name)" :src="item.imgurl" lazy-load="https://www.firstsite.cn/logo.png"></image>
								<view class="text">{{ item.son_name }}</view>
							</view>
						</view>
					</view>
				</view>
